import React, {FunctionComponent} from "react"
import { Link as GatsbyLink } from "gatsby"

// Abstraction for links, so we don't have dependency on Gatsby in our components.
// If more props needed, add them here
export interface AppLinkProps {
    activeClassName?: string;
    replace?: boolean;
    to: string;
    className?: string;
}

const AppLink: FunctionComponent<AppLinkProps> = ({children, ...props}) => {
    return (
        <GatsbyLink {...props}>
            {children}
        </GatsbyLink>
    )
};

export default AppLink;
