import React, {FunctionComponent} from 'react';
import getComponent from "../../../utils/cms/component-resolver";

import ClassNames from './SplitContainer.module.scss';
import classNames from 'classnames'
import AppLink from "../../../util-components/Link/Link";

interface Props {
    data: any
}

// not possible, cannot add vars to query. Check if we can resolve full model for page in createPages.js
const SplitContainer: FunctionComponent<Props> = ({data}) => {
    const {area1, area2} = data.fields;

    function toggleRtl() {
        document.documentElement!.classList.toggle('rtl');
    }
    function renderArea(index, items) {
        return (
            <div className={classNames(ClassNames.area, ClassNames['area' + index])}>
                <h3>Area{index} from CMS SplitContainer</h3>
                <div>{items.map(item => getComponent(item))}</div>
                {index === 2 && (
                    <div>
                        <div><AppLink to="/second-page">Second page (hard link now)</AppLink></div>
                        <div><button onClick={toggleRtl}>Toggle RTL</button></div>
                    </div>
                )}
            </div>
        );
    }
    return (
        <div className={ClassNames.SplitContainer}>
            {renderArea(1, area1)}
            {renderArea(2, area2)}
        </div>
    );
};

export default SplitContainer;
